.des {
    padding: 4rem 0rem;
    background: #fff;
  }
  
  .des-h1 {
    text-align: center;
  }
  
  .des__container {
    display: flex;
    flex-flow: column;
    align-items: center;
    margin: 0 auto;
  }
  
  .des__wrapper {
    position: relative;
    margin: 50px 0 45px;
    width: 100%;
  }
  
  .des__items {
    margin-bottom: 24px;
  }
  
  .des__item {
    display: flex;
    flex: 1;
    margin: 0 1rem;
    border-radius: 10px;
  }
  
  .des__item__link {
    display: flex;
    flex-flow: column;
    width: 100%;
    -webkit-filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
    filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
    overflow: hidden;
    text-decoration: none;
  }
  
  .des__item__pic-wrap {
    position: relative;
    width: 100%;
    padding-top: 67%;
    overflow: hidden;
  }
  
  .fade-img {
    animation-name: fade-img;
    animation-duration: 2s;
  }
  
  .des__item__img {
    position: absolute;
    top: 0;
    display: block;
    border-radius: 3px;
    width: 100%;
    max-width: 100%;
    height: 100%;
    max-height: 100%;
  }
  
  .des__item__info {
    padding: 20px 0;
    width: 100%;
    height: 100%;
  }
  .des__item__p{
    padding: 5px 0 0 20px;
  }
  .des__item__text {
    color: #252e48;
    font-size: 18px;
    line-height: 24px;
  }
  
  @media only screen and (min-width: 1200px) {
    .content__blog__container {
      width: 84%;
    }
  }
  
  @media only screen and (min-width: 1024px) {
    .des__items {
      display: flex;
    }
  }
  
  @media only screen and (max-width: 1024px) {
    .des__item {
      margin-bottom: 2rem;
    }
  }
