:root{
    --primary:  #FF4500 ;
}

.btn{
    padding: 8px 20px;
    border-radius: 2px;
    outline: none;
    border: none;
    cursor: pointer;
    font-weight: bold;
}

.btn--primary{
    background-color: var(--primary);
    color: #242424;
    border: 1px solid var(--primary);
}

.btn--outline{
    background-color: transparent;
    color:  #FF4500 ;
    
    border: 1px solid var(--primary);
    transition: all 0.3s ease-out;
}

.btn--medium{
    width: auto;
    font-size: 18px;
}

.btn--large{
    padding: 12px 26px;
    font-size: 20px;
}

.btn--medium:hover, .btn--large:hover{
    background: #fff;
    color: #242224;
    transition: all 0.3s ease-out;
    border: 1px solid #fff;
}